
html,body{margin:0;padding:0;font-size:12px;font-family:"Microsoft YaHei",Arial,Helvetica,sans-serif;background:#fff;color:#222;min-height:990px;-webkit-tap-highlight-color:transparent
}
ul,span,p,h1,h2,h3,a{margin:0;padding:0
}
a{color:black;text-decoration:none
}
li{list-style-type:none
}
.hide{display:none
}
#index-top{height:240px;margin-bottom:20px
}
.container{width:1160px;clear:both;margin:auto
}
.container .b-l{float:left;width:440px
}
.container .b-r{float:right;width:720px
}
.b-section{height:360px;margin-bottom:20px;color:white;background-color:#00a1d6;font-size:20px;text-align:center;line-height:360px
}
#donghua,#yinyue,#keji,#guanggao{background-color:#2ecc71
}
#fanjv,#wudao,#shenghuo,#yule{background-color:#324057
}
#guochuang,#youxi,#guichu,#dianying{background-color:#f05b72
}
.footer{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;height:1000px;color:#fffffb;background-color:#e5e9f2
}
.footer a{width:100%;text-align:center;text-decoration:none;font-size:60px;color:#2980b9
}
.footer p{width:100%;text-align:center;color:black
}
@media screen and (max-width: 1400px){
.container{width:980px
}
.container .b-r{width:540px
}
#promote .b-l{width:700px
}
}



