
.index-nav[data-v-7]{position:fixed;position:fixed;width:48px;z-index:90;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;opacity:1;display:block;top:232px;left:1545px;right:auto
